.login-container {
  width: 300px;
  margin: 0 auto;
  margin-top: 100px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f9f9f9;
}

.login-container h2 {
  text-align: center;
}

.login-container input {
  width: calc(100% - 22px);
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.login-container button {
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #2848a7;
  color: #fff;
  cursor: pointer;
}

.login-container button:hover {
  background-color: #216288;
}

.error {
  color: red;
  margin: 10px 0;
  text-align: center;
}

.messages {
  margin-top: 20px;
}

.message-card {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
  background-color: #fff;
}

.message-card p {
  margin: 5px 0;
}

.message-card p strong {
  display: inline-block;
  width: 100px;
}
